.nav a{
    
    color:rgb(17, 2, 2);
    text-decoration: dashed;
    padding:30px;
    border-radius: 10px;
    margin-left: 20px;
    font-size: 20px;
    font-weight: bold;
}

.nav a{
    text-decoration: none;
    color: white;
    margin: 0 15px;
}
.nav{
    display: flex;
    list-style: none;
}
@media (min-width: 768px) {
    
    .mobile-menu-icon{
        display: none;
    }
   
    
    .nav-container{
        
        /* background: linear-gradient(90deg,yellow,green); */
        
        align-items: center;
        display: flex;
        justify-content: space-around;
        
        
    }
    .nav a{
    
        color:rgb(17, 2, 2);
        text-decoration: dashed;
        padding:0;
        border-radius: 10px;
        margin-left: 20px;
        font-size: 20px;
        font-weight: bold;
    }
}
@media (max-width: 768px) {
    .logo{
        display: none;
    }
    .nav{
        display: none;
    }
    .nav-container{
        
        flex-direction: column;
        width: 100%;
    }
    .mobile-menu-icon{
        display: block;
        font-size: 30px;
    }
    .nav-links-mobile{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        justify-content: center;
        align-items: center;
        z-index: 1;
        background-color: black;
        position: absolute;
        top: 50px;
        left: 0;
    }
    
    .nav-links-mobile  a{
        text-decoration: none;
        color: white;
        font-size: 30px;
        margin-bottom: 50px;
        border-bottom: 2px solid white;
    }
}
